import React, {useContext} from 'react'
import {FeatureToggleService} from './FeatureToggleService';
import * as styles from '../components/footer.module.css';
import {CookieConsentContext} from '../contexts/cookie-consent-context';

export const PrivacyChoiceLink = (): JSX.Element => {
    const { setPopupVisible } = useContext(CookieConsentContext)

    if (FeatureToggleService.privacyChoiceFeatureFlag.isEnabled) {
        return (
            <li className={styles.menuItem}>
                <button id='show-cookie-consent' className={styles.privacyLink} onClick={() => {setPopupVisible(true)}}> Your Privacy Choices </button>
            </li>
        );
    }
    return <></>
}
