// tslint:disable:max-classes-per-file

export const checkCookie = (name: string, value: string = 'true') => {
  if (typeof document === 'undefined') {
    /// This is being rendered through the server instead of the browser. So assume cookie does not exist
    return false;
  }
  return document.cookie.indexOf(`${name}=${value}`) !== -1;
};

class PublicLeaderboardsFeatureFlag {
  get isEnabled() {
    return process.env.GATSBY_PUBLIC_LEADERBOARD_ENABLED === 'true' || checkCookie('display-public-leaderboards');
  }
}

