class MarketExitFeatureFlag {
  get isEnabled(): boolean {
    return Date.now() >= Date.parse('2024-01-23T00:00:00.000-07:00') || checkCookie('market-exit')
  }

  get geoblockedMarketsUSA(): String {
    if (Date.now() >= Date.parse('2024-04-22T00:00:00.000-07:00') || checkCookie('geoblock-MT')) {
      return 'WA, MI and MT states'
    } else if (Date.now() >= Date.parse('2024-02-01T01:00:00.000-04:00') || checkCookie('geoblock-MI')) {
      return 'WA and MI states'
    } else {
      return 'WA state'
    }
  }

  get geoblockedMarketsCA(): String {
    return 'Quebec'
  }
}

