import React, { ReactNode } from 'react'
import { Link as GatsbyLink, navigate as gatsbyNavigate } from 'gatsby'

interface LinkProps {
	to: string
	children: ReactNode
	className?: string
	onClick?: () => void
	title?: string
}

export default function Link({ to, children, className, onClick, title }: LinkProps) {
	return (
		<GatsbyLink
			to={to}
			className={className}
			onClick={onClick}
			title={title}
		>
			{children}
		</GatsbyLink>
	)
}

export function navigate(to: string) {
	gatsbyNavigate(to)
}
